:root {
    --bouncy-easing: cubic-bezier(0.68, -0.55, 0.265, 1.55);
    --cell-bg: linear-gradient(0deg, rgba(255,255,255,.5) 0%, rgba(255,255,255,.2) 100%);
}

html,
body {
    height: 100vh;
    width: 100%;
    overflow: auto;
}

.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100vh;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: hidden;
}

.table {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding-right: 9%;
    max-width: 500px;
    margin: 0 auto;
    border-spacing: 1px;
}

.table__col {
    width: 9%;
    position: relative;
    border-radius: 1px;
    background-image: var(--cell-bg);
}

.table__col:before {
    content: '';
    display: block;
    padding-top: 50%;
}

.table__head {
    color: rgba(255,255,255,.5);
    font-weight: normal;
    font-size: .75rem;
    background: none;
}

.numbers {
    background-color: #08AEEA;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#08AEEA), to(#2AF598));
    background-image: -webkit-linear-gradient(top, #08AEEA 0%, #2AF598 100%);
    background-image: -o-linear-gradient(top, #08AEEA 0%, #2AF598 100%);
    background-image: linear-gradient(180deg, #08AEEA 0%, #2AF598 100%);
    padding-bottom: 100px;
    -webkit-transition: all .2s ease-in;
    -o-transition: all .2s ease-in;
    transition: all .2s ease-in;
}
.numbers--fail {
    background-color: #FAD961;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#FAD961), color-stop(99%, #F76B1C));
    background-image: -webkit-linear-gradient(bottom, #FAD961 0%, #F76B1C 99%);
    background-image: -o-linear-gradient(bottom, #FAD961 0%, #F76B1C 99%);
    background-image: linear-gradient(0deg, #FAD961 0%, #F76B1C 99%);
    -webkit-transition: all .2s ease-in;
    -o-transition: all .2s ease-in;
    transition: all .2s ease-in;
}

.number {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 2rem;
}

.number--byOne {
    /*background-color: #08AEEA;*/
}

.number--sameNumbers {
    /*background-color: #2AF598;*/
}

.display {
    text-align: center;
}

.selected {
    text-align: center;
    font-size: 1rem;
    color: #fff;
    line-height: 0;
    height: 1rem;

}

.number--selected {
    background-image: var(--cell-bg), url('data:image/svg+xml,\
          <svg xmlns="http://www.w3.org/2000/svg" width="50%" height="50%" fill-opacity="1" fill="#fff">\
            <ellipse cx="50%" cy="50%" rx="25%" ry="25%" fill-color="red"/>\
          </svg>');
}

.tip:after {
    content: '';
    display: block;
    border-radius: 50%;
    width: 50%;
    height: 50%;
    background-color: #fff;
    position: absolute;
    top: 25%;
    left: 25%;
    -webkit-animation: tipAnimation 1s linear infinite;
            animation: tipAnimation 1s linear infinite;
    pointer-events: none;
}

@-webkit-keyframes tipAnimation {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: .8;
    }
    100% {
        -webkit-transform: scale(3);
                transform: scale(3);
        opacity: .1;
    }
}

@keyframes tipAnimation {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: .8;
    }
    100% {
        -webkit-transform: scale(3);
                transform: scale(3);
        opacity: .1;
    }
}


