.display {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    padding: .5rem 1rem;
    color: #08AEEA;
}
.task {
}
.solutions {
    padding: 0;
    display: inline-block;
}
.solution {
    min-width: 2.6rem;
    font-size: .8rem;
    color: #666;
    padding-top: .1rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 1.2rem;
    display: inline-block;
    line-height: 1rem;
    text-align: center;
    border-radius: .5rem;
    background-color: #e2e2e2;
    margin: 0 .15rem;
    -webkit-transition: background-color .2s ease-in;
    -o-transition: background-color .2s ease-in;
    transition: background-color .2s ease-in;
}
.solution--correct {
    background-color: #08AEEA;
    color: #fff;
    -webkit-animation: correctSolutionAnimation .3s var(--bouncy-easing) .25s;
            animation: correctSolutionAnimation .3s var(--bouncy-easing) .25s;
}
.correct {
    /* @keyframes duration | timing-function | delay |
iteration-count | direction | fill-mode | play-state | name */
    -webkit-animation: correctAnimation .3s var(--bouncy-easing) .25s;
            animation: correctAnimation .3s var(--bouncy-easing) .25s;
}
.fail {
    -webkit-animation: failAnimation .2s linear;
            animation: failAnimation .2s linear;
}
@-webkit-keyframes correctSolutionAnimation {
    0%, 100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }
    75% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}
@keyframes correctSolutionAnimation {
    0%, 100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }
    75% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}
@-webkit-keyframes correctAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(.8);
                transform: scale(.8);
    }
    50% {
        color: #2AF598;
    }
    75% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
@keyframes correctAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(.8);
                transform: scale(.8);
    }
    50% {
        color: #2AF598;
    }
    75% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
@-webkit-keyframes failAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    25% {
        -webkit-transform: translateX(20%);
                transform: translateX(20%);
        color: #F76B1C;
    }
    50% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
        -webkit-transform: translateX(-20%);
                transform: translateX(-20%);
    }
}
@keyframes failAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    25% {
        -webkit-transform: translateX(20%);
                transform: translateX(20%);
        color: #F76B1C;
    }
    50% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
        -webkit-transform: translateX(-20%);
                transform: translateX(-20%);
    }
}


